:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.form-section {
  z-index: 100;
  width: 100%;
  text-align: center;
  background-color: #5e5ef7;
  padding: 10px;
  position: fixed;
  top: 0;
  box-shadow: 0 0 10px #000;
}

.search-form {
  height: 30px;
  max-width: 300px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.search-input {
  height: 100%;
  width: 100%;
  border: 1px solid rgba(136, 136, 136, .533);
  border-right: none;
  border-radius: 5px;
  outline: none;
  padding: 0 5px;
  display: block;
}

.btn-search {
  width: 30px;
  height: 30px;
  text-align: center;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  outline: none;
  justify-content: center;
  align-items: center;
  margin-left: -30px;
  padding: 0;
  display: flex;
}

.btn-search:hover {
  cursor: pointer;
}

.search-input:focus, .btn-search:focus {
  border: 1px solid #4a4a4a;
}

.gallery-section {
  margin-top: 50px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  display: flex;
}

.gallery__item {
  width: calc(25% - 15px);
  color: #000;
  min-height: 144px;
  transition: transform var(--animation-duration) var(--timing-function);
  border-radius: 5px;
  flex-direction: column;
  text-decoration: none;
  display: flex;
  overflow: hidden;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .1), 0 1px 1px rgba(0, 0, 0, .1), 0 1px 1px -1px rgba(0, 0, 0, .1);
}

.gallery__item:hover {
  transform: scale(1.03);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.img-container {
  aspect-ratio: 4 / 3;
  object-fit: cover;
  overflow: hidden;
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
}

.info-item {
  margin: 0;
}

.info {
  text-align: center;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  gap: 10px;
  padding: 10px 5px;
  font-size: 12px;
  display: flex;
}

.btn-container {
  text-align: center;
}

.load-more {
  opacity: .8;
  color: #fff;
  background-color: #5e5ef7;
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px;
}

.load-more:hover {
  opacity: 1;
  cursor: pointer;
}

.btn-hidden {
  display: none;
}

